.App {
    min-height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    grid-gap: 50px;
    display: grid;
    grid-template-areas: 
        'name link'
        'experience education'
        'info courses';
}

.name {
    grid-area: name
}

.links {
    grid-area: link;
    display: flex;
    align-items: center;
}
.links__item {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 5px;
    cursor: pointer;
}
.links__item > img {
    width: 24px;
    height: 24px;
    border-radius: 100px;
    -webkit-border-radius: 100px;
    -moz-border-radius: 100px;
    -ms-border-radius: 100px;
    -o-border-radius: 100px;
}

.title {
    font-size: 2rem;
}

.subtitle {
    font-size: 1.3rem;
    color: #8A8A8A;
}

.text {
    transform: translateX(15px);
    position: relative;
    font-size: 1.2rem;
    margin-top: 10px;
    -webkit-transform: translateX(15px);
    -moz-transform: translateX(15px);
    -ms-transform: translateX(15px);
    -o-transform: translateX(15px);
}
.text::before {
    content: '';
    position: absolute;
    left: -10px;
    top: 50%;
    transform: translateX(-50%);
    width: 4px;
    height: 4px;
    background: white;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
}

.experience {
    grid-area: experience;
}

.education {
    grid-area: education;
}

.info {
    grid-area: info;
}

.courses {
    grid-area: courses;
}

@media screen and (max-width: 900px) {
    .container {
        padding: 100px 40px;
        grid-template-areas: 
            'name'
            'link'
            'experience'
            'education'
            'courses'
            'info';
    }
}